import React from 'react';
import {Icon, Layout, Menu} from 'antd';
import {Route} from 'react-router-dom';
import Activity from "../common/Activity";
import ClassManageActivity from "./ClassManageActivity";
import AccountManageActivity from "./AccountManageActivity";
import AddAdminActivity from "./AddAdminActivity";
import ResetPsdActivity from "./ResetPsdActivity";

const {Header, Content, Footer, Sider} = Layout;


const keyAdmin = 1;
const keyAccount = 2;
const keyClassManage = 3;
const keyLog = 4;
const keyReset = 5;

export default class AdminIndexActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            hasLogin: localStorage.hasLogin === 'true',
        };
    }

    componentWillMount() {
        super.componentWillMount();
    }

    onMenuItemClick(item) {
        switch (Number(item.key)) {
            case keyAdmin:
                this.props.history.push('/admin/add/');
                break;
            case keyAccount:
                this.props.history.push('/admin/account/');
                break;
            case keyClassManage:
                this.props.history.push('/admin/');
                break;
            case keyReset:
                this.props.history.push('/admin/psd/');
                break;
            case keyLog:
                localStorage.hasLogin = false;
                this.props.history.push('/login/');
                break;
        }
    }

    render() {

        return (
            <Layout>
                <Sider style={{overflow: 'auto', height: '100vh', position: 'fixed', left: 0}}>
                    <div className="logo"/>
                    <Menu theme="dark" mode="inline" defaultSelectedKeys={[keyClassManage + ""]}
                          onClick={item => this.onMenuItemClick(item)}>
                        <Menu.Item key={keyClassManage}>
                            <Icon type="video-camera"/>
                            <span className="nav-text">班级管理</span>
                        </Menu.Item>
                        <Menu.Item key={keyAccount}>
                            <Icon type="bar-chart"/>
                            <span className="nav-text">账户管理</span>
                        </Menu.Item>
                        <Menu.Item key={keyAdmin}>
                            <Icon type="user"/>
                            <span className="nav-text">添加管理员</span>
                        </Menu.Item>
                        <Menu.Item key={keyReset}>
                            <Icon type="user"/>
                            <span className="nav-text">修改密码</span>
                        </Menu.Item>
                        <Menu.Item key={keyLog}>
                            <Icon type="upload"/>
                            <span className="nav-text">{this.state.hasLogin == true ? "退出登录" : '登录'}</span>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Layout style={{marginLeft: 200}}>
                    <Header style={{padding: 0}}/>
                    <Content style={{margin: '16px 16px 0', overflow: 'auto'}}>
                        <Route exact path={`${this.props.match.url}/class/`} component={ClassManageActivity}/>
                        <Route exact path={`${this.props.match.url}/account/`} component={AccountManageActivity}/>
                        <Route exact path={`${this.props.match.url}/add/`} component={AddAdminActivity}/>
                        <Route exact path={`${this.props.match.url}/psd/`} component={ResetPsdActivity}/>
                        <Route exact path={`${this.props.match.url}`} component={ClassManageActivity}/>
                    </Content>
                </Layout>
            </Layout>);
    }
}


