import React from 'react';
import {Button, Form, Icon, Input, Radio, Divider} from 'antd';
import './login.css';
import HttpClient from '../utils/HttpClient';
import {Link} from "react-router-dom";
import Activity from "../common/Activity";

var md5 = require('md5');

const FormItem = Form.Item;
const keyStudent = 0;
const keyTeacher = 1;
const keyAdmin = 2;

class LoginActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            role: keyStudent
        };
    }

    handleSubmit = (e) => {
        this.showLoading();
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                let url = '/api/' + (this.state.role == keyStudent ? 'student' : this.state.role == keyTeacher ? 'teacher' : 'admin') + '/account/';
                HttpClient.get(url, {num: values.num, psd: md5(values.password)}).then(json => {
                    this.hideLoading();
                    localStorage.hasLogin = true;
                    if (this.state.role == keyStudent) {
                        this.props.history.push('/student/');
                    } else if (this.state.role == keyTeacher) {
                        this.props.history.push('/teacher/');
                    }else if (this.state.role == keyAdmin) {
                        this.props.history.push('/admin/');
                    }
                }).catch(error => {
                    this.hideLoading();
                    if (error.code > 0) {
                        if(error.code == 1){
                            alert('账号或密码错误！');
                        }else {
                            alert(error.toString());
                        }
                    } else {
                        HttpClient.handleCommonError(this.props.history, error)
                    }
                });
            }else {
                this.hideLoading();
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        return this.createLoading(
            <Form onSubmit={this.handleSubmit} className="login-form">
                <Divider><div style={{fontSize:40}}>学生考勤系统</div></Divider>
                <FormItem>
                    <Radio.Group value={this.state.role} onChange={e => this.setState({role: e.target.value})}>
                        <Radio.Button value={keyStudent}>学生</Radio.Button>
                        <Radio.Button value={keyTeacher}>教师</Radio.Button>
                        <Radio.Button value={keyAdmin}>管理员</Radio.Button>
                    </Radio.Group>
                </FormItem>
                <FormItem>
                    {getFieldDecorator('num', {
                        rules: [{required: true, message: '学号或工号不能为空!'}],
                    })(
                        <Input prefix={<Icon type="user" style={{color: 'rgba(0,0,0,.25)'}}/>} placeholder="学号/工号"/>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('password', {
                        rules: [{required: true, message: '密码不能为空!', min:6}],
                    })(
                        <Input prefix={<Icon type="lock" style={{color: 'rgba(0,0,0,.25)'}}/>} type="password"
                               placeholder="密码"/>
                    )}
                </FormItem>
                <FormItem>
                    <Button type="primary" htmlType="submit" className="login-form-button">
                        登录
                    </Button>
                    或 <Link to="/signup/">去注册</Link>
                </FormItem>
            </Form>
        );
    }
}

export default Form.create()(LoginActivity);



