import React from 'react';
import {Icon, Layout, Menu} from 'antd';
import {Route} from 'react-router-dom';
import './index.css';
import Activity from "../common/Activity";
import ClassManageActivity from "./ClassManageActivity";
import LeaveManageActivity from "./LeaveManageActivity";
import ResetPsdActivity from "./ResetPsdActivity";
import StudentManageActivity from "./StudentManageActivity";
import StuInfoActivity from "./StuInfoActivity";

const {Header, Content, Footer, Sider} = Layout;


const keyInfo = 1;
const keyLeaveList = 2;
const keyClassManage = 3;
const keyLog = 4;
const keyPsd = 5;

export default class TeacherIndexActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            hasLogin: localStorage.hasLogin === 'true',
        };
    }

    componentWillMount() {
        super.componentWillMount();
    }

    onMenuItemClick(item) {
        switch (Number(item.key)) {
            case keyInfo:
                //this.props.history.push('/student/');
                break;
            case keyLeaveList:
                this.props.history.push('/teacher/leave/');
                break;
            case keyClassManage:
                this.props.history.push('/teacher/class/');
                break;
            case keyPsd:
                this.props.history.push('/teacher/psd/');
                break;
            case keyLog:
                localStorage.hasLogin = false;
                this.props.history.push('/login/');
                break;
        }
    }

    render() {

        return (
            <Layout>
                <Sider style={{overflow: 'auto', height: '100vh', position: 'fixed', left: 0}}>
                    <div className="logo"/>
                    <Menu theme="dark" mode="inline" defaultSelectedKeys={[keyClassManage + ""]}
                          onClick={item => this.onMenuItemClick(item)}>
                        <Menu.Item key={keyClassManage}>
                            <Icon type="video-camera"/>
                            <span className="nav-text">班级管理</span>
                        </Menu.Item>
                        <Menu.Item key={keyLeaveList}>
                            <Icon type="bar-chart"/>
                            <span className="nav-text">请假审核</span>
                        </Menu.Item>
                        <Menu.Item key={keyPsd}>
                            <Icon type="user"/>
                            <span className="nav-text">修改密码</span>
                        </Menu.Item>
                        <Menu.Item key={keyLog}>
                            <Icon type="upload"/>
                            <span className="nav-text">{this.state.hasLogin == true ? "退出登录" : '登录'}</span>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Layout style={{marginLeft: 200}}>
                    <Header style={{padding: 0}}/>
                    <Content style={{margin: '16px 16px 0', overflow: 'auto'}}>
                        <Route exact path={`${this.props.match.url}/class/`} component={ClassManageActivity}/>
                        <Route exact path={`${this.props.match.url}/class/:id/`} component={StudentManageActivity}/>
                        <Route exact path={`${this.props.match.url}/student/:id/`} component={StuInfoActivity}/>
                        <Route exact path={`${this.props.match.url}/leave/`} component={LeaveManageActivity}/>
                        <Route exact path={`${this.props.match.url}/psd/`} component={ResetPsdActivity}/>
                        <Route exact path={`${this.props.match.url}`} component={ClassManageActivity}/>
                    </Content>
                </Layout>
            </Layout>);
    }
}


