import Activity from "../common/Activity";
import {Tabs, Table, Form, Button, Input} from "antd";
import React from "react";
import HttpClient from "../utils/HttpClient";

const TabPane = Tabs.TabPane;
const FormItem = Form.Item;

class ClassManageActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            classes: []
        }
    }

    componentWillMount() {
        this.showLoading();
        HttpClient.get('/api/admin/clazz/').then(data => {
            if (data.count != 0) {
                this.setState({classes: data.list});
            }
            this.hideLoading();
        }).catch(error => {
            super.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    columns = [{
        title: '名称',
        dataIndex: 'name',
    }, {
        title: '专业',
        dataIndex: 'profession',

    }, {
        title: '学生数',
        dataIndex: 'student_count',
        // }, {
        //     title: '删除班级',
        //     dataIndex: 'id',
        //     render: (text, record) => <Button onClick={() => this.deleteClass(text)}>删除</Button>
    }];

    handleSubmit = e => {
        this.showLoading();
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                let url = '/api/admin/clazz/';
                let clazz = {name: values.name, profession: values.profession};
                HttpClient.post(url, clazz).then(data => {
                    clazz.student_count = 0;
                    this.state.classes.push(clazz);
                    this.hideLoading();
                    alert('添加成功');
                }).catch(error => {
                    this.hideLoading();
                    if (error.code > 0) {
                        console.log(error);
                    } else {
                        HttpClient.handleCommonError(this.props.history, error)
                    }
                });
            } else {
                this.hideLoading();
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        return this.createLoading(
            <Tabs defaultActiveKey="2">
                <TabPane tab="班级列表" key="2">
                    <Table
                        pagination={false}
                        dataSource={this.state.classes}
                        columns={this.columns}/>
                </TabPane>
                <TabPane tab="添加班级" key="1">
                    <Form onSubmit={this.handleSubmit}>
                        <FormItem label="班级名称">
                            {getFieldDecorator('name', {
                                rules: [{required: true, message: '班级名称不能为空!'}],
                            })(
                                <Input/>
                            )}
                        </FormItem>
                        <FormItem label="专业名称">
                            {getFieldDecorator('profession', {
                                rules: [{required: true, message: '专业名称不能为空!'}],
                            })(
                                <Input/>
                            )}
                        </FormItem>
                        <FormItem>
                            <Button type="primary" htmlType="submit"> 添加 </Button>
                        </FormItem>
                    </Form>
                </TabPane>

            </Tabs>
        )
    }
}

export default Form.create()(ClassManageActivity);


