import Activity from "../common/Activity";
import {Tabs, Table, Form, Button, Input} from "antd";
import React from "react";
import HttpClient from "../utils/HttpClient";

const TabPane = Tabs.TabPane;
const FormItem = Form.Item;

class AddAdminActivity extends Activity {

    constructor(props) {
        super(props);
    }

    handleSubmit = e => {
        this.showLoading();
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                let url = '/api/admin/account/';
                let account = {name: values.name, num: values.num};
                HttpClient.post(url, account).then(data => {
                    this.hideLoading();
                    alert('添加成功');
                }).catch(error => {
                    this.hideLoading();
                    if (error.code > 0) {
                        console.log(error);
                    } else {
                        HttpClient.handleCommonError(this.props.history, error)
                    }
                });
            } else {
                this.hideLoading();
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;
        return this.createLoading(
            <Form onSubmit={this.handleSubmit}>
                <FormItem label="账号">
                    {getFieldDecorator('num', {
                        rules: [{required: true, message: '账号不能为空!'}],
                    })(
                        <Input/>
                    )}
                </FormItem>
                <FormItem label="姓名">
                    {getFieldDecorator('name', {
                        rules: [{required: true, message: '姓名不能为空!'}],
                    })(
                        <Input/>
                    )}
                </FormItem>
                <FormItem>
                    <Button type="primary" htmlType="submit"> 确定 </Button>
                </FormItem>
            </Form>
        )
    }
}

export default Form.create()(AddAdminActivity);


