import Activity from "../common/Activity";
import {Button, Table, Tabs} from "antd";
import React from "react";
import HttpClient from "../utils/HttpClient";

const TabPane = Tabs.TabPane;
const keyStudent = 'student';
const keyTeacher = 'teacher';
const keyAdmin = 'admin';
export default class AccountManageActivity extends Activity {
    constructor(props) {
        super(props);
        this.state = {
            students: [],
            teachers: [],
            admins: [],
            count: 0,
            currentTab: keyStudent,
        }
    }

    columns = [{
        title: '姓名',
        dataIndex: 'name',
    }, {
        title: '账号',
        dataIndex: 'num',

    }, {
        title: '操作',
        dataIndex: 'num',
        render: (text, record) =>
            <div>
                <Button onClick={() => this.resetPsd(text)}>重置密码</Button>
                <Button onClick={() => this.deleteAccount(text)}>删除</Button>
            </div>
    }];

    componentWillMount() {
        let params = [keyAdmin, keyStudent, keyTeacher];
        for (let i = 0; i < params.length; i++) {
            HttpClient.get('/api/admin/account/' + params[i] + '/').then(data => {
                let state = {count: this.state.count + 1};
                if (data.count != 0) {
                    state[params[i] + 's'] = data.list;
                }
                this.setState(state);
                this.hideLoading();
            }).catch(error => {
                super.hideLoading();
                if (error.code > 0) {
                    console.log(error);
                } else {
                    HttpClient.handleCommonError(this.props.history, error)
                }
            });
        }
    }

    deleteAccount(id){
        this.showLoading();
        HttpClient.delete('/api/admin/account/'+this.state.currentTab+'/'+id+'/').then(data => {
            let accounts = this.state[this.state.currentTab+'s'];
            for (let i = 0; i < accounts.length; i++) {
                let account = accounts[i];
                if (id == account.num) {
                    accounts.splice(i, 1);
                    let state = {};
                    state[this.state.currentTab+'s'] = accounts;
                    this.setState(state);
                    super.hideLoading();
                    alert('删除成功');
                    break;
                }
            }

        }).catch(error => {
            this.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    resetPsd(num) {
        this.showLoading();
        HttpClient.delete('/api/admin/psd/' + this.state.currentTab + '/' + num + '/').then(data => {
            super.hideLoading();
            alert('重置密码成功');
        }).catch(error => {
            super.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    hideLoading() {
        if (this.state.count == 3) {
            super.hideLoading();
            this.setState({count: 0});
        }
    }

    render() {
        return this.createLoading(
            <Tabs defaultActiveKey={keyStudent} onChange={actKey => this.setState({currentTab: actKey})}>
                <TabPane tab="学生" key={keyStudent}>
                    <Table
                        pagination={false}
                        dataSource={this.state.students}
                        columns={this.columns}/>
                </TabPane>
                <TabPane tab="教师" key={keyTeacher}>
                    <Table
                        pagination={false}
                        dataSource={this.state.teachers}
                        columns={this.columns}/>
                </TabPane>
                <TabPane tab="管理员" key={keyAdmin}>
                    <Table
                        pagination={false}
                        dataSource={this.state.admins}
                        columns={this.columns}/>
                </TabPane>
            </Tabs>)
    }
}


