import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router, Route} from 'react-router-dom'
import './index.css';
import registerServiceWorker from './registerServiceWorker';
import SignupActivity from "./signup/SignupActivity";
import LoginActivity from "./login/LoginActivity";
import StudentIndexActivity  from "./student/StudentIndexActivity"
import TeacherIndexActivity from "./teacher/TeacherIndexActivity";
import AdminIndexActivity from "./admin/AdminIndexActivity";

var routers = (
    <Router>
        <div>
            <Route path="/student/" component={StudentIndexActivity}/>
            <Route path="/teacher/" component={TeacherIndexActivity}/>
            <Route path="/admin/" component={AdminIndexActivity}/>
            <Route exact path="/login/" component={LoginActivity}/>
            <Route exact path="/signup/" component={SignupActivity}/>
        </div>
    </Router>
);

ReactDOM.render(routers, document.getElementById('root'));
registerServiceWorker();



