import React, {Component} from 'react';
import HttpClient from '../utils/HttpClient';
import {List, Icon, Layout, Menu, Button, Spin, Input, Avatar, Card, Form} from 'antd';
import {Link, Route} from 'react-router-dom';
import Activity from "../common/Activity";

const FormItem = Form.Item;

class LeaveApplyActivity extends Activity {

    constructor(props) {
        super(props);
    }

    handleSubmit = e=>{
        this.showLoading();
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                let url = '/api/student/leave/';
                HttpClient.post(url, {date: values.date, reason:values.reason}).then(json => {
                    this.hideLoading();
                    alert('请假申请已提交');
                    this.props.history.push('/student/leave/')
                }).catch(error => {
                    this.hideLoading();
                    if (error.code > 0) {
                        console.log(error);
                    } else {
                        HttpClient.handleCommonError(this.props.history, error)
                    }
                });
            }else {
                this.hideLoading();
            }
        });
    };

    render() {
        const {getFieldDecorator} = this.props.form;

        return this.createLoading(
            <Form onSubmit={this.handleSubmit} style={{padding:50}}>
                <FormItem label="请假时间">
                    {getFieldDecorator('date', {
                        rules: [{
                            required: true, message: '请假时间不能为空!',
                        }],
                    })(
                        <Input type="date"/>
                    )}
                </FormItem>
                <FormItem label="请假理由">
                    {getFieldDecorator('reason', {
                        rules: [{
                            required: true, message: '请假理由不能为空!',
                        }],
                    })(
                        <Input.TextArea style={{width:300}} autosize={{minRows:5}}/>
                    )}
                </FormItem>
                <FormItem>
                    <Button type="primary" htmlType="submit">提交</Button>
                </FormItem>

            </Form>
        );
    }


}

export default Form.create()(LeaveApplyActivity);


