export default class HttpClient {
    static get(url = '', params) {

        if (params) {
            let paramsArray = [];
            Object.keys(params).forEach(key => paramsArray.push(key + '=' + params[key]));
            if (url.search(/\?/) === -1) {
                url += '?' + paramsArray.join('&')
            } else {
                url += '&' + paramsArray.join('&')
            }
        }

        return new Promise(function (resolve, reject) {
            fetch(url, {
                method: 'GET',
                credentials: 'same-origin'
            }).then((response) => {
                //如果出现网络错误，直接跳到catch，
                //只要reject就会跳到最外面的catch
                if (response.ok) {
                    return response.json();
                } else {
                    reject({code: response.status, desc: '网络或服务器错误！'});
                    console.log(response);
                }
            }).then((json) => {
                if (json.code == 0) {
                    resolve(json.data);
                } else {
                    reject(json);
                }
            }).catch((error) => {
                alert(error);
                reject(error);
            })
        })
    }

    static handleCommonError(history, error) {
        switch (error.code) {
            case -1:
                alert('用户登录过期，请重新登录');
                localStorage.hasLogin = false;
                history.push('/login/');
                break;
            case -4:
                alert('缺少参数' + error.data.param_name);
                break;
            default:
                console.log(error);
        }
    }

    static post(url = '', params) {

        let formData = new FormData();

        for (let key in params) {
            formData.append(key, params[key]);
        }

        return new Promise(function (resolve, reject) {
            fetch(url, {
                method: 'POST',
                body: formData,
                credentials: 'same-origin'
            }).then((response) => {
                if (response.ok) {
                    return response.json();
                } else {
                    reject({code: response.status, desc: '网络或服务器错误！'});
                    console.log(response);
                }
            }).then((json) => {
                if (json.code == 0) {
                    resolve(json.data);
                } else {
                    reject(json);
                }
            }).catch((error) => {
                alert(error);
                reject(error);
            })
        })
    }

    static delete(url = '', params) {

        if (params) {
            let paramsArray = [];
            Object.keys(params).forEach(key => paramsArray.push(key + '=' + params[key]));
            if (url.search(/\?/) === -1) {
                url += '?' + paramsArray.join('&')
            } else {
                url += '&' + paramsArray.join('&')
            }
        }

        return new Promise(function (resolve, reject) {
            fetch(url, {
                method: 'DELETE',
                credentials: 'same-origin'
            }).then((response) => {
                //如果出现网络错误，直接跳到catch，
                //只要reject就会跳到最外面的catch
                if (response.ok) {
                    return response.json();
                } else {
                    reject({code: response.status, desc: '网络或服务器错误！'});
                    console.log(response);
                }
            }).then((json) => {
                if (json.code == 0) {
                    resolve(json.data);
                } else {
                    reject(json);
                }
            }).catch((error) => {
                alert(error);
                reject(error);
            })
        })
    }

}




