import React, {Component} from 'react';
import {Spin} from "antd";
import './activity.css';

export default class Activity extends Component {
    constructor(props) {
        super(props);
        this.state = {};
    }

    componentWillMount(){
        if(localStorage.hasLogin !== 'true'){
            this.props.history.push('/login/');
        }
    }

    createLoading(component) {
        return <Spin spinning={this.state.loading==undefined?false:this.state.loading} delay={500}>{component}</Spin>
    }

    showLoading() {
        this.setState({loading: true});
    }

    hideLoading() {
        this.setState({loading: false});
    }
}


