import React from 'react';
import {Table, Divider} from 'antd';
import Activity from "../common/Activity";
import HttpClient from "../utils/HttpClient";

const columns = [{
    title: '时间',
    dataIndex: 'date',

}, {
    title: '原因',
    dataIndex: 'reason',
}, {
    title: '状态',
    dataIndex: 'state',
    render: (state) => state == 0 ? '待审核' : state == 1 ? '通过' : '驳回'
}];


export default class LeaveListActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            leaves: [],
            chidao: 0,
            zaotui: 0,
            chufen: 0,
            count: 0
        }
    }

    componentWillMount() {
        this.showLoading();
        HttpClient.get('/api/student/attendance/').then(data => {
            this.setState({count: this.state.count + 1, ...data});
            this.hideLoading();
        }).catch(error => {
            super.hideLoading();
            this.setState({count: 0});
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
        HttpClient.get('/api/student/leave/').then(data => {
            let state = {count: this.state.count + 1};
            if (data.count != 0) {
                state.leaves = data.list;
            }
            this.setState(state);
            this.hideLoading();
        }).catch(error => {
            super.hideLoading();
            this.setState({count: 0});
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    hideLoading() {
        if (this.state.count == 2) {
            super.hideLoading();
            this.setState({count: 0});
        }
    }


    render() {
        return this.createLoading(
            <div>
                <Divider>出勤情况</Divider>
                <div style={{display: 'flex'}}>
                    <div style={{flex: 1}}>{'迟到次数：' + this.state.chidao}</div>
                    <div style={{flex: 1}}>{'早退次数：' + this.state.zaotui}</div>
                    <div style={{flex: 1}}>{'处分次数：' + this.state.chufen}</div>
                </div>
                <Divider>请假记录</Divider>
                <Table pagination={false} columns={columns} dataSource={this.state.leaves}/>

            </div>
        );
    }


}


