import React from 'react';
import {Table, Divider, Button} from 'antd';
import Activity from "../common/Activity";
import HttpClient from "../utils/HttpClient";

export default class LeaveManageActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            students: [],
        }
    }

    columns = [{
        title: '姓名',
        dataIndex: 'student_name',
    }, {
        title: '班级',
        dataIndex: 'class_name',
    }, {
        title: '时间',
        dataIndex: 'date',
    }, {
        title: '原因',
        dataIndex: 'reason',
    }, {
        title: '状态',
        dataIndex: 'state',
        render: (state, option) => state == 0 ?
            <div style={{display: 'flex'}}>
                <Button onClick={()=>this.verifyLeave(option.id, true)}>通过</Button>
                <Button onClick={()=>this.verifyLeave(option.id, false)}>驳回</Button>
            </div> : state == 1 ? '已通过' : '已驳回'
    }];

    componentWillMount() {
        this.showLoading();
        HttpClient.get('/api/teacher/leave/').then(data => {
            if(data.count!=0){
                this.setState({students:data.list});
            }
            this.hideLoading();
        }).catch(error => {
            this.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    verifyLeave(id, allow){
        this.showLoading();
        HttpClient.post('/api/teacher/leave/'+id+'/', {allow:allow}).then(data=>{
            let leaves = this.state.students;
            for(let i=0; i<leaves.length; i++){
                let leave = leaves[i];
                if(leave.id==id){
                    this.state.students[i].state = allow?1:2;
                    break;
                }
            }
            this.hideLoading();
        }).catch(error => {
            this.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    render() {
        return this.createLoading(
            <Table pagination={false} columns={this.columns} dataSource={this.state.students}/>
        );
    }


}


