import React, {Component} from 'react';
import HttpClient from '../utils/HttpClient';
import {Button, Form, Input, Radio, Select} from 'antd';
import './signup.css';
import Activity from "../common/Activity";

var md5 = require('md5');
const Option = Select.Option;
const FormItem = Form.Item;
const keyStudent = 0;
const keyTeacher = 1;

class SignupActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            role: keyStudent,
            clazzes: [],
        };
    }

    componentWillMount() {
        HttpClient.get('/api/student/class/').then(data => {
            if (data.count != 0) {
                this.setState({
                    clazzes: data.list,
                });
            }
        }).catch(error => {
            if (error.code < 0) {
                HttpClient.handleCommonError(this.props.history, error);
            } else {
                console.log(error);
            }
        });
    }

    handleSubmit = (e) => {
        this.showLoading();
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (!err) {
                let params = {num: values.num, psd: md5(values.password), name: values.name};
                if (this.state.role == keyStudent) {
                    var t = 'student';
                    params.clazz_id = values.clazz;
                } else {
                    var t = 'teacher';
                }
                let url = '/api/' + t + '/account/';
                HttpClient.post(url, params).then(data => {
                    this.hideLoading();
                    alert('注册成功，去登录。');
                    this.props.history.push('/login/');
                }).catch(error => {
                    this.hideLoading();
                    if (error.code < 0) {
                        HttpClient.handleCommonError(this.props.history, error);
                    } else {
                        if (error.code == 1) {
                            alert('该学号/工号已存在，请重新注册。')
                        } else {
                            console.log(error);
                        }
                    }
                });

            } else {
                this.hideLoading();
            }
        });
    };

    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('两次密码不一致!');
        } else {
            callback();
        }
    };


    render() {
        const {getFieldDecorator} = this.props.form;


        return this.createLoading(
            <Form onSubmit={this.handleSubmit} className="signup-form">
                <FormItem>
                    <Radio.Group value={this.state.role} onChange={e => this.setState({role: e.target.value})}>
                        <Radio.Button value={keyStudent}>学生</Radio.Button>
                        <Radio.Button value={keyTeacher}>教师</Radio.Button>
                    </Radio.Group>
                </FormItem>
                <FormItem>
                    {getFieldDecorator('name', {
                        rules: [{
                            required: true, message: '姓名不能为空!',
                        }],
                    })(
                        <Input placeholder="姓名"/>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('num', {
                        rules: [{
                            required: true, message: '学号或工号不能为空!',
                        }],
                    })(
                        <Input placeholder="学号/工号"/>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: '密码不能少于6位!',min:6,
                        },],
                    })(
                        <Input type="password" placeholder="密码"/>
                    )}
                </FormItem>
                <FormItem>
                    {getFieldDecorator('confirm', {
                        rules: [{
                            required: true, message: '确认密码不能为空!',
                        }, {
                            validator: this.compareToFirstPassword,
                        }],
                    })(
                        <Input type="password" placeholder="确认密码"/>
                    )}
                </FormItem>
                {
                    this.state.role == keyStudent ?
                        <FormItem>
                            {getFieldDecorator('clazz', {
                                rules: [{
                                    required: true, message: '所在班级不能为空!',
                                }],
                            })(
                                <Select
                                    showSearch
                                    style={{width: 200}}
                                    placeholder="请选择所在班级"
                                    optionFilterProp="children"
                                    filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                                >
                                    {this.state.clazzes.map(clazz => <Option key={clazz.id}
                                                                             value={clazz.id}>{clazz.name}</Option>)}
                                </Select>)}
                        </FormItem> : undefined
                }
                <FormItem>
                    <Button type="primary" htmlType="submit">注册</Button>
                </FormItem>
            </Form>
        );
    }
}

export default Form.create()(SignupActivity)


