import React from 'react';
import Activity from "../common/Activity";
import {Table, Select, Button} from "antd";
import HttpClient from "../utils/HttpClient";

const Option = Select.Option;
export default class ClassManageActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            myClasses: [],
            classes: [],
            count: 0,
        }
    }

    columns = [{
        title: '名称',
        dataIndex: 'name',
    }, {
        title: '专业',
        dataIndex: 'profession',

    }, {
        title: '学生数',
        dataIndex: 'student_count',
    }, {
        title: '管理',
        dataIndex: 'id',
        render: (text, record) =>
            <div>
                <Button onClick={() => this.props.history.push('/teacher/class/'+text+'/')}>查看</Button>
                <Button onClick={() => this.deleteClass(text)}>删除</Button>
            </div>
    }];

    componentWillMount() {
        HttpClient.get('/api/teacher/myclass/').then(data => {
            let state = {count: this.state.count + 1};
            if (data.count != 0) {
                state.myClasses = data.list;
            }
            this.setState(state);
            this.hideLoading();
        }).catch(error => {
            super.hideLoading();
            this.setState({count: 0});
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });

        HttpClient.get('/api/teacher/class/').then(data => {
            let state = {count: this.state.count + 1};
            if (data.count != 0) {
                state.classes = data.list;
            }
            this.setState(state);
            this.hideLoading();
        }).catch(error => {
            super.hideLoading();
            this.setState({count: 0});
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    deleteClass(id) {
        this.showLoading();
        HttpClient.delete('/api/teacher/myclass/' + id + '/').then(data => {
            let myClasses = this.state.myClasses;
            for (let i = 0; i < myClasses.length; i++) {
                let clazz = myClasses[i];
                if (id == clazz.id) {
                    this.state.classes.push(clazz);
                    myClasses.splice(i, 1);
                    this.setState({myClasses: myClasses});
                    break;
                }
            }
            super.hideLoading();
        }).catch(error => {
            super.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    addClass() {
        this.showLoading();
        let id = this.state.addClassId;
        if (id == undefined) {
            super.hideLoading();
            alert('请选择要管理的班级');
            return;
        }
        HttpClient.post('/api/teacher/myclass/' + id + '/').then(data => {
            let classes = this.state.classes;
            for (let i = 0; i < classes.length; i++) {
                let clazz = classes[i];
                if (id == clazz.id) {
                    this.state.myClasses.push(clazz);
                    classes.splice(i, 1);
                    this.setState({classes: classes, addClassId: undefined});
                    break;
                }
            }
            super.hideLoading();
        }).catch(error => {
            super.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    hideLoading() {
        if (this.state.count == 2) {
            super.hideLoading();
            this.setState({count: 0});
        }
    }

    render() {
        return this.createLoading(
            <div style={{display: 'flex'}}>
                <div style={{flex: 1}}>
                    <Table
                        pagination={false}
                        dataSource={this.state.myClasses}
                        columns={this.columns}/>
                </div>
                <div style={{
                    flex: 1,
                    display: 'flex',
                    justifyContent: 'center',
                    flexDirection: 'column',
                    alignItems: 'center'
                }}>

                    <Select
                        showSearch
                        value={this.state.addClassId}
                        onSelect={(value, option) => this.setState({addClassId: value})}
                        style={{width: 200}}
                        placeholder="请选择要管理的班级"
                        optionFilterProp="children"
                        filterOption={(input, option) => option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0}
                    >
                        {this.state.classes.map(clazz => <Option key={clazz.id}
                                                                 value={clazz.id}>{clazz.name}</Option>)}
                    </Select>

                    <Button onClick={() => this.addClass()}>添加</Button>

                </div>


            </div>
        );
    }

}


