import React from 'react';
import {Table, Divider, Button} from 'antd';
import Activity from "../common/Activity";
import HttpClient from "../utils/HttpClient";

export default class StudentManageActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            students: [],
        }
    }

    columns = [{
        title: '姓名',
        dataIndex: 'name',
    }, {
        title: '学号',
        key:'num',
        dataIndex: 'num',
    }, {
        title: '性别',
        dataIndex: 'sex',
    }, {
        title: '迟到次数',
        dataIndex: 'chidao',
    }, {
        title: '早退次数',
        dataIndex: 'zaotui',
    }, {
        title: '处分次数',
        dataIndex: 'chufen',
    }, {
        title: '管理',
        dataIndex: 'num',
        key:'action',
        render: (text, record) =><div>
                <Button onClick={() => this.props.history.push('/teacher/student/' + text + '/')}>查看</Button>
                <Button onClick={() => this.deleteStudent(text)}>删除</Button>
            </div>


    }];

    deleteStudent(id){
        this.showLoading();
        HttpClient.delete('/api/teacher/student/'+id+'/').then(data => {
            let students = this.state.students;
            for (let i = 0; i < students.length; i++) {
                let student = students[i];
                if (id == student.num) {
                    students.splice(i, 1);
                    this.setState({students: students});
                    this.hideLoading();
                    alert('删除成功');
                    break;
                }
            }

        }).catch(error => {
            this.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }

    componentWillMount() {
        this.showLoading();
        HttpClient.get('/api/teacher/student/class/' + this.props.match.params.id + '/').then(data => {
            if (data.count != 0) {
                this.setState({students: data.list});
            }
            this.hideLoading();
        }).catch(error => {
            this.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });
    }



    render() {
        return this.createLoading(
            <Table pagination={false} columns={this.columns} dataSource={this.state.students}/>
        );
    }


}


