import React from 'react';
import {Icon, Layout, Menu} from 'antd';
import {Route} from 'react-router-dom';
import './index.css';
import LeaveApplyActivity from "./LeaveApplyActivity";
import LeaveListActivity from "./LeaveListActivity";
import Activity from "../common/Activity";
import InfoActivity from "./InfoActivity";
import ResetPsdActivity from "./ResetPsdActivity";

const {Header, Content, Footer, Sider} = Layout;


const IconText = ({type, text}) => (
    <span>
        <Icon type={type} style={{marginRight: 8}}/>
        {text}</span>
);

const keyInfo = 1;
const keyLeaveList = 2;
const keyLeaveApply = 3;
const keyLog = 4;
const keyPsd = 5;

export default class StudentIndexActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            hasLogin: localStorage.hasLogin === 'true',
        };
    }

    componentWillMount() {
        super.componentWillMount();
    }

    onMenuItemClick(item) {
        switch (Number(item.key)) {
            case keyInfo:
                this.props.history.push('/student/');
                break;
            case keyLeaveList:
                this.props.history.push('/student/leave/');
                break;
            case keyLeaveApply:
                this.props.history.push('/student/leave/apply/');
                break;
            case keyPsd:
                this.props.history.push('/student/psd/');
                break;
            case keyLog:
                localStorage.hasLogin = false;
                this.props.history.push('/login/')
                break;
        }
    }

    render() {

        return (
            <Layout>
                <Sider style={{overflow: 'auto', height: '100vh', position: 'fixed', left: 0}}>
                    <div className="logo"/>
                    <Menu theme="dark" mode="inline" defaultSelectedKeys={[keyInfo + ""]}
                          onClick={item => this.onMenuItemClick(item)}>
                        <Menu.Item key={keyInfo}>
                            <Icon type="user"/>
                            <span className="nav-text">我的资料</span>
                        </Menu.Item>
                        <Menu.Item key={keyLeaveList}>
                            <Icon type="bar-chart"/>
                            <span className="nav-text">考勤记录</span>
                        </Menu.Item>
                        <Menu.Item key={keyLeaveApply}>
                            <Icon type="video-camera"/>
                            <span className="nav-text">请假申请</span>
                        </Menu.Item>
                        <Menu.Item key={keyPsd}>
                            <Icon type="video-camera"/>
                            <span className="nav-text">修改密码</span>
                        </Menu.Item>
                        <Menu.Item key={keyLog}>
                            <Icon type="upload"/>
                            <span className="nav-text">{this.state.hasLogin == true ? "退出登录" : '登录'}</span>
                        </Menu.Item>
                    </Menu>
                </Sider>
                <Layout style={{marginLeft: 200}}>
                    <Header style={{padding: 0}}/>
                    <Content style={{margin: '16px 16px 0', overflow: 'auto'}}>
                        <Route exact path={`${this.props.match.url}/leave/apply/`} component={LeaveApplyActivity}/>
                        <Route exact path={`${this.props.match.url}/leave/`} component={LeaveListActivity}/>
                        <Route exact path={`${this.props.match.url}/psd/`} component={ResetPsdActivity}/>
                        <Route exact path={`${this.props.match.url}`} component={InfoActivity}/>
                    </Content>
                </Layout>
            </Layout>);
    }
}


