import React from 'react';
import Activity from "../common/Activity";
import {Button, Form, Input, Radio, InputNumber} from "antd";
import {Link} from "react-router-dom";
import HttpClient from "../utils/HttpClient";
import './info.css';

const FormItem = Form.Item;

class StuInfoActivity extends Activity {

    constructor(props) {
        super(props);
        this.state = {
            num: '',
            name: '',
            clazz_name: '',
            clazz_profession: '',

            id: '',
            sex: '',
            politics_status: '',
            huji: '',
            nation: '',
            origin: '',
            tele: '',
            email: '',
            birth: '',
            date_in_school: '',
            chidao:0,
            zaotui:0,
            chufen:0,
        };
    }

    componentWillMount() {
        this.showLoading();
        HttpClient.get('/api/teacher/student/'+this.props.match.params.id+'/').then(data => {
            this.setState({...data});
            this.hideLoading();
        }).catch(error => {
            this.hideLoading();
            if (error.code < 0) {
                HttpClient.handleCommonError(this.props.history, error)
            } else {
                console.log(error);
            }
        });
    }

    saveInfo(params) {
        this.showLoading();
        let url = '/api/teacher/student/'+this.props.match.params.id+'/';
        HttpClient.post(url, params).then(json => {
            this.hideLoading();
            alert('保存成功');
            this.props.history.goBack();
        }).catch(error => {
            this.hideLoading();
            if (error.code > 0) {
                console.log(error);
            } else {
                HttpClient.handleCommonError(this.props.history, error)
            }
        });

    }

    verify_args() {
        let args = ['birth', 'sex', 'politics_status', 'origin', 'huji'];
        let ex = ['出生日期', '性别', '政治面貌', '生源地', '户籍所在地'];
        let result = {};
        for (let i = 0; i < args.length; i++) {
            let param = args[i];
            let value = this.state[param];
            if (value == '') {
                alert(ex[i] + '不能为空');
                return undefined;
            } else {
                result[param] = value;
            }
        }
        args = ['chufen', 'zaotui', 'chidao'];
        ex = ['处分次数', '早退次数', '迟到次数'];
        for (let i = 0; i < args.length; i++) {
            let param = args[i];
            let value = this.state[param];
            if (value>=0 && value%1==0) {
                result[param] = value;
            } else {
                alert(ex[i] + '为正整数');
                return undefined;
            }
        }
        let id = this.state.id;
        if (id.match(/\d{17}[\dxX]/)) {
            result['id'] = id;
        } else {
            alert('身份证号不符合规范');
            return undefined;
        }
        let tele = this.state.tele;
        if (tele.match(/\d{11}/)) {
            result['tele'] = tele;
        } else {
            alert('电话号码不符合规范');
            return undefined;
        }
        let email = this.state.email;
        if (email.match(/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/)) {
            result['email'] = email;
        } else {
            alert('邮箱不符合规范');
            return undefined;
        }
        return result;
    }

    render() {
        return this.createLoading(
            <Form>
                <div style={{display: 'flex'}}>
                    <div className="my-info" style={{alignItems: 'center'}}>
                        <FormItem label="姓名" className="my-row">
                            <Input disabled={true} value={this.state.name}/>
                        </FormItem>
                        <FormItem label="专业" className="my-row">
                            <Input disabled={true} value={this.state.clazz_profession}/>
                        </FormItem>
                        <FormItem label="出生日期" className="my-row">
                            <Input type="date" value={this.state.birth}
                                   onChange={(e) => this.setState({birth: e.target.value})}/>
                        </FormItem>
                        <FormItem label="身份证号" className="my-row">
                            <Input value={this.state.id} onChange={(e) => this.setState({id: e.target.value})}/>
                        </FormItem>
                        <FormItem label="政治面貌" className="my-row">
                            <Input value={this.state.politics_status}
                                   onChange={(e) => this.setState({politics_status: e.target.value})}/>
                        </FormItem>
                        <FormItem label="入校时间" className="my-row">
                            <Input type="date" value={this.state.date_in_school}
                                   onChange={(e) => this.setState({date_in_school: e.target.value})}/>
                        </FormItem>
                        <FormItem label="迟到次数" className="my-row">
                            <Input type="number" value={this.state.chidao}
                                   onChange={(e) => this.setState({chidao: Number(e.target.value)})}/>
                        </FormItem>
                        <FormItem label="早退次数" className="my-row">
                            <Input type="number" value={this.state.zaotui}
                                   onChange={(e) => this.setState({zaotui: Number(e.target.value)})}/>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" htmlType="submit" onClick={() => {
                                let params = this.verify_args();
                                if (params != undefined) {
                                    this.saveInfo(params);
                                }
                            }}> 保存 </Button>
                        </FormItem>
                    </div>
                    <div className="my-info">
                        <FormItem label="学号" className="my-row">
                            <Input disabled={true} value={this.state.num}/>
                        </FormItem>
                        <FormItem label="班级" className="my-row">
                            <Input disabled={true} value={this.state.clazz_name}/>
                        </FormItem>
                        <FormItem label="性别" className="my-row">
                            <Radio.Group value={this.state.sex} onChange={(e) => this.setState({sex: e.target.value})}>
                                <Radio.Button value="男">男</Radio.Button>
                                <Radio.Button value="女">女</Radio.Button>
                            </Radio.Group>
                        </FormItem>
                        <FormItem label="电话" className="my-row">
                            <Input value={this.state.tele} onChange={(e) => this.setState({tele: e.target.value})}/>
                        </FormItem>
                        <FormItem label="邮箱" className="my-row">
                            <Input value={this.state.email} type="email"
                                   onChange={(e) => this.setState({email: e.target.value})}/>
                        </FormItem>
                        <FormItem label="生源地" className="my-row">
                            <Input value={this.state.origin} onChange={(e) => this.setState({origin: e.target.value})}/>
                        </FormItem>
                        <FormItem label="处分次数" className="my-row">
                            <Input type="number" value={this.state.chufen}
                                         onChange={(e) => this.setState({chufen: Number(e.target.value)})}/>
                        </FormItem>
                        <FormItem label="户籍所在地" className="my-row">
                            <Input value={this.state.huji} onChange={(e) => this.setState({huji: e.target.value})}/>
                        </FormItem>
                        <FormItem>
                            <Button type="primary" htmlType="submit" onClick={() => {
                                this.props.history.goBack()
                            }}> 返回 </Button>
                        </FormItem>
                    </div>


                </div>


            </Form>
        );
    }
}

export default Form.create()(StuInfoActivity)


