import Activity from "../common/Activity";
import {Button, Form, Input, Tabs} from "antd";
import React from "react";
import HttpClient from "../utils/HttpClient";

const FormItem = Form.Item;
var md5 = require('md5');

class ResetPsdActivity extends Activity {

    constructor(props) {
        super(props);
    }

    handleSubmit = e => {
        this.showLoading();
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                let url = '/api/teacher/psd/';
                let account = {old:md5(values.old), new:md5(values.password)};
                HttpClient.post(url, account).then(data => {
                    this.hideLoading();
                    alert('修改密码成功，重新登录！');
                    localStorage.hasLogin=false;
                    this.props.history.push('/login/');
                }).catch(error => {
                    this.hideLoading();
                    if (error.code > 0) {
                        if(error.code==1){
                            alert('原密码错误')
                        }
                        console.log(error);
                    } else {
                        HttpClient.handleCommonError(this.props.history, error)
                    }
                });
            } else {
                this.hideLoading();
            }
        });
    };

    compareToFirstPassword = (rule, value, callback) => {
        const form = this.props.form;
        if (value && value !== form.getFieldValue('password')) {
            callback('两次密码不一致!');
        } else {
            callback();
        }
    };
    render() {
        const {getFieldDecorator} = this.props.form;
        return this.createLoading(
            <Form onSubmit={this.handleSubmit}>
                <FormItem label="原密码">
                    {getFieldDecorator('old', {
                        rules: [{
                            required: true, message: '原密码不能为空!',
                        },],
                    })(
                        <Input type="password" placeholder="密码"/>
                    )}
                </FormItem>
                <FormItem label="新密码">
                    {getFieldDecorator('password', {
                        rules: [{
                            required: true, message: '密码不能少于六位!',min:6
                        },],
                    })(
                        <Input type="password" placeholder="新密码"/>
                    )}
                </FormItem>
                <FormItem label="确认密码">
                    {getFieldDecorator('confirm', {
                        rules: [{
                            required: true, message: '确认密码不能为空!',
                        }, {
                            validator: this.compareToFirstPassword,
                        }],
                    })(
                        <Input type="password" placeholder="确认密码"/>
                    )}
                </FormItem>
                <FormItem>
                    <Button type="primary" htmlType="submit"> 添加 </Button>
                </FormItem>
            </Form>
        )
    }
}

export default Form.create()(ResetPsdActivity);


